.containerR {
  margin: 0rem 9rem 2rem;
  display: grid;
  grid-template-areas:
    "col2 col2 col2 col1 col1 col1"
    "col3 col3 col3 col1 col1 col1";
  grid-gap: 1rem;

  @media screen and (max-width: 1220px) {
    width: auto;
    margin: 4rem 2rem;
    display: block;
  }

  @media screen and (max-width: 850px) {
    width: auto;
    margin: 3rem;
    display: block;
  }

  @media screen and (max-width: 450px) {
    width: auto;
    margin: auto;
    display: block;
  }
}
.photoR {
  width: 28rem;
  height: 28rem;
  grid-area: col1;
  margin-left: 8rem;
  margin-top: 7rem;

  @media screen and (max-width: 1220px) {
    width: auto;
    margin: 3rem auto;
    display: block;
  }

  @media screen and (max-width: 850px) {
    width: auto;
    margin: 3rem auto;
    display: block;
  }

  @media screen and (max-width: 450px) {
    width: auto;
    margin: 3rem auto;
    display: block;
  }
}
.titleR {
  color: #ebebeb;
  grid-area: col2;
  cursor: default;
  margin-top: 5rem;

  @media screen and (max-width: 1220px) {
    width: auto;
    margin: 2rem 0;
    font-size: 28px;
  }

  @media screen and (max-width: 850px) {
    width: auto;
    margin: 2rem 0;
    font-size: 28px;
  }

  @media screen and (max-width: 450px) {
    width: auto;
    margin: 2rem;
    font-size: 28px;
  }
}
.bodyR {
  color: #ebebeb;
  line-height: 1.75rem;
  letter-spacing: 1px;
  grid-area: col3;

  @media screen and (max-width: 450px) {
    width: auto;
    margin: 2rem;
    font-size: 16px;
  }
}
