footer {
  background-color: #212121;
  height: 40vh;
  width: auto;
  display: grid;
  grid-template-areas: "follow contact legal";
  grid-gap: 15rem;
  padding: 10em 3rem;

  @media screen and(max-width: 1550px) {
    grid-gap: 15rem;
    padding: 8em 1rem;
    height: auto;
  }

  @media screen and (max-width: 1220px) {
    width: auto;
    margin: auto;
    display: block;
    height: auto;
  }
  @media screen and (max-width: 450px) {
    width: auto;
    margin: auto;
    display: block;
    height: auto;
  }
}
.socials {
  color: #ebebeb;
  margin-left: 1rem;

  @media screen and (max-width: 1225px) {
    width: auto;
    margin-top: -6rem;
    margin-bottom: 3rem;
    display: block;
  }
  @media screen and (max-width: 450px) {
    width: auto;
    margin-top: -6rem;
    margin-bottom: 3rem;
    display: block;
  }
  @media screen and (max-width: 325px) {
    margin-left: 1rem;
  }
}
.legal {
  color: #ebebeb;
  margin-left: 4rem;
  margin-right: 3rem;
  font-size: 12px;

  span {
    font-size: 12px;
    margin-bottom: 0.5rem;
    display: block;
  }

  p {
    letter-spacing: 1px;
    cursor: default !important;
    max-width: 350px;
  }

  @media screen and (max-width: 1220px) {
    width: auto;
    margin: 4rem 1rem 0rem;
    display: block;
    font-size: 12px;
  }

  @media screen and (max-width: 450px) {
    width: auto;
    margin: 4rem 1rem 0rem;
    display: block;
    font-size: 12px;
  }
  @media screen and (max-width: 325px) {
    margin-left: 1rem;
  }
}
h3 {
  margin-bottom: 2rem;
  letter-spacing: 1px;
  cursor: default !important;
}
p {
  letter-spacing: 1px;
  cursor: default !important;
}
.contact {
  margin-right: 1rem;
  margin-bottom: 2rem;

  @media screen and (max-width: 1220px) {
    width: auto;
    margin: 4rem 1rem;
    display: block;
  }

  @media screen and (max-width: 450px) {
    width: auto;
    margin: 4rem 1rem;
    display: block;
  }
  @media screen and (max-width: 325px) {
    margin-left: 1rem;
  }
}
.form-title {
  color: #ebebeb;
}
.form-group {
  transition: all 0.3s;
  cursor: default;

  &:focus-within {
    transform: scale(1.05, 1.05);
  }
}
.form-control {
  background: none;
  outline: none;
  resize: none;
  border: none;
  border-bottom: 2px #01ff3b solid;
  margin-bottom: 2rem;
  color: #fff;
  font-size: 1rem;
  transition: all 0.5s;

  &::placeholder {
    color: transparent;
  }
  &:focus {
    border-color: orange;
  }
  &:focus + .form-label {
    transform: translateY(-4.5em) scale(0.8);
  }
  &:not(:placeholder-shown) + .form-label {
    transform: translateY(-4.5em) scale(0.8);
  }
  &:focus + .messaging {
    transform: translateY(-12.5em) scale(0.8);
  }
  &:not(:placeholder-shown) + .messaging {
    transform: translateY(-12.5em) scale(0.8);
  }
}
.btn-submit {
  padding: 1rem 2rem;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 4px;
  outline: none;
  border: none;
  background-color: #01ff3b;

  &:hover {
    background-color: #fff;
  }
}
.thank {
  background-color: #01ff3b;
  border-radius: 16px;
  padding: 0.5rem;
  margin-bottom: 2rem;

  @media screen and (max-width: 450px) {
    width: auto;
    display: block;
    margin-left: -1rem;
    font-size: 20px;
    padding: 1rem;
  }
}
.form-label {
  color: #ebebeb;
  display: block;
  position: absolute;
  transform: translateY(-3.4em);
  transform-origin: 0 0;
  transition: all 0.3s;
  cursor: default;
}
.messaging {
  color: #ebebeb;
  display: block;
  position: absolute;
  transform: translateY(-11.3em);
  transform-origin: 0 0;
  transition: all 0.3s;
  cursor: default;
}
.social {
  color: #ebebeb;
  margin-right: 1rem;
}
.facebook:hover {
  color: #4968ad;
}
.twitter:hover {
  color: #49a1eb;
}
.insta:hover {
  color: rgb(193, 53, 132);
}
